<!-- Concat.vue -->
<template>
	<div class="ui-concat-wrapper ui-detail-common">
		<div class="header zhiCustomBtn">
			<p></p>
			<!-- <a href="https://www.sobot.com/chat/h5/index.html?sysNum=5b696ae2d5394f06b558e64ebbaf3be7&back=1&titleFlag=2&customTtile=''&title_info='__'&url_info='http://www.sobot.com/images/logo.png'&abstract_info=''&label_info=200&thumbnail_info='http://www.sobot.com/images/logo.png'">rrrr</a> -->
			<img src="../../../assets/image/change-icon/e5_service@2x.png">
		</div>
	</div>
</template>

<script>
 	import { mapState, mapMutations } from 'vuex';
	export default {
		data () {
			return {
			}
		},

		computed: mapState({
			user: state => state.auth.user
		}),

		created(){
			var zhiManager = (getzhiSDKInstance());
		    //  load  js 
		    zhiManager.on("load", function(ret) {
		        zhiManager.initBtnDOM();
		        zhiManager.set('lazyFlag', true);
		        zhiManager.set('robotHelloWord', '!  ');
		        zhiManager.set('thumbnail_info', 'http://www.sobot.com/images/logo.png');
		        zhiManager.set('label_info', '200');
		        zhiManager.set('abstract_info', '');
		        zhiManager.set('url_info', 'http://www.sobot.com/images/logo.png');
		        zhiManager.set('title_info', '__');
		        zhiManager.set('back', 1);
		        zhiManager.set('titleFlag', 2);
		        zhiManager.set('customTtile', '')
		        zhiManager.set('customBtn', 'true');
		        zhiManager.set('title', '');
		    });
		},

		methods: {
		}
	}
</script>

<style lang="scss" scoped>
	.ui-concat-wrapper {
		p{
			font-size:16px;
			font-family:PingFangSC-Regular;
			color:rgba(78,84,93,1);
			line-height:20px;
			padding: 0px;
			margin: 0px;
		}
		img {
			width:12px;
			height:13px;
			cursor: pointer;
		}
	}
</style>



